.candidate-conference .video-section-border {
	border: 0!important;
}
.candidate-conference .test-right {
	top: 320px;
}

.candidate-conference1 #controls {
	left: 20%;
}
.candidate-conference .test-header-react {
	padding: 10px !important;
}
.candidate-conference .icon {
	position: fixed;
	top: 60px;
}
	.candidate-conference .icon.icon-chat {
		top: unset;
		bottom: 10px;
	}
.candidate-conference .sidebar {
	top: 60px;
	padding: 0;
}
.candidate-conference .peer-video-tile,
.candidate-conference .shape-circle,
.candidate-conference .peer-video {
	border-radius: 0;
	margin-bottom: 0;
}
.candidate-conference .shape-circle {
	aspect-ratio: 1/1;
	height: 220px;
	width: 100%;
}
.candidate-conference .sidebar.chat-box {
	top: unset;
	bottom: 0;
	padding: 8px;
}
.candidate-conference .btn-close {
	position: absolute;
	left: 10px;
	width: 6px;
	height: 6px;
}

.candidate-conference .messages {
	height: 250px;
	max-height: 250px;
}
.candidate-conference .peer-name {
	display: none;
}

.candidate-conference .peer-network {
	position: absolute;
	top: 0;
	padding-left: 3px;
}

.candidate-conference .connection-network {
	background-color: transparent;
}

.btn-control {
	display: inline-block;
	background: #f30446;
	color: #fff;
	font-weight: 500;
	padding: 10px 10px;
	margin: 5px;
	border-radius: 0px 100px 100px 0px;
	width: 130px;
	text-align: center;
	transition: 0.4s;
	border: 0;
}

.btn-left-rounded {
	border-radius: 100px 0px 0px 100px;
}

.btn-control:hover,
.btn-no-rounded {
	border-radius: 0px;
}

.test-right {
	top: 320px;
}
